.App {
  text-align: center;
  
  height: 100%;
  max-width: 756px;
  margin: 0 auto;
}

.message-box{
  max-height: 750px;
  overflow-y: auto;
  overflow-x: hidden;
  padding: 10px;
  border-radius: 5px;
  display: flex;
  flex-direction: column-reverse;
}

#root {
  height: 100%;
}



body {
  margin: 0;
  padding: 0;
  height: 100%;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
